<template>
    <!-- <div class="search"><span id="search-label">Search</span> -->
    <div>
        <span>Search</span>
        <span>
            <!-- <input type="text" placeholder="Enter search word" class="search-box"> -->
            <input
                type="text"
                placeholder="Enter search word"
                v-model="quick_search_string"
            />
            <button @click="getSearchResults">Search</button>
            <button @click="doClear">Clear</button>
        </span>
        <div>This searches authors, keywords, title and description</div>
    </div>
    <div v-if="error">Error: unable to query server!</div>
    <div v-else class="flex-container">
        <div>
            <p><b>Filters</b></p>
            <div v-for="item in filters" :key="item">
                <!-- filter = {{ item }} -->
                <FilterItem :item="item"></FilterItem>
                <hr />
            </div>
        </div>
        <div>
            <p><b>Search Results:</b> {{ search_result.["hits-total"] }}</p>
            <div v-for="(item, index) in search_result.results" :key="item">
                <ResultItem :index="index + 1" :item="item" />
                <hr />
            </div>
        </div>
    </div>

    <!-- <div v-if="error">Error: unable to query server!</div>
  <div v-else>
    <div v-for="(value, name) in facet_names" :key="value.subject">
      facet = {{ name }} - key = {{ value }}
    </div>
    <div>
      <p>Search Results: {{ search_result.["hits-total"] }}</p>
      <div v-for="(item, index) in search_result.results" :key="item">
        <ResultItem :index="index + 1" :item="item" />
        <hr />
      </div>
    </div>
  </div> -->
</template>

<script>
import FilterItem from "./FilterItem.vue";
import ResultItem from "./ResultItem.vue";

export default {
    props: {
        msg: String,
    },

    error: "",
    // is_querying: false,
    name: "Search",

    components: { FilterItem, ResultItem },

    data() {
        return {
            error: false,
            facet_names: {
                keywords: ["Keywords", "show"],
                author_org: ["Author Organizations", "show"],
                language_primary: ["Primary Languages", "show"],
                languages_secondary: ["Secondary Languages", "show"],
                target_audience: ["Target Audiences", "show"],
                license: ["License", "show"],
                "accessibility_features.name": [
                    "Accessibility Features",
                    "show",
                ],
                subject: ["Resource Subject", "show"],
                publisher: ["Resource Publisher", "show"],
                usage_info: ["Usage Information", "show"],
                media_type: ["Media Type", "show"],
                lr_type: ["Learning Resource Type", "show"],
                purpose: ["Resource Purpose", "show"],
                ed_frameworks: ["Educational Frameworks", "hide"],
            },
            filters: [],
            items_per_page: 10,
            offset: 0,
            quick_search_string: "",
            search_result: [],
            sort_str: "score desc",
        };
    },

    methods: {
        getSearchResults() {
            //console.log("entered getSearchResults()");
            console.log("quick_search_string = ", this.quick_search_string);

            this.error = false;
            // this.is_querying = true;
            let body = this.setupPostContent();
            //console.log("body: ", body);

            fetch("https://esip-dev-02.edacnm.org/api/resources/", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    //'Access-Control-Allow-Headers': '*',
                },
                body: JSON.stringify(body),
            })
                .then((response) => {
                    if (response.ok) {
                        // DO NOT do anything to the response, including console.log
                        // or else it will cause an error that response is "disturbed"
                        return response.json();
                    } else {
                        throw new Error();
                    }
                })
                .then((result) => {
                    this.isLoading = false;
                    console.log("xresult :", result);
                    /*
                const results = [];
                for ( const key in data ) {
                    console.log ( "key = " + key );
                    console.log ( "value = " + data[key] );
                    results.push ( {
                        key: key,
                        //name: data[id].name,
                        //rating: data[id].rating
                    });
                }
                */
                    this.search_result = result;
                    this.filters = this.setupFilters(result.facets);
                    // this.is_querying = false;
                    //console.log("search_result :", this.search_result);
                })
                .catch(() => {
                    this.error = true;
                });
        },

        setupFilters(result_filters) {
            // console.log("setupFilters: ", result_filters);
            // console.log("keywords : ", result_filters["keywords"]);
            //console.log("1 facet_names", this.facet_names);
            const keys1 = Object.keys(this.facet_names);
            //console.log("keys1 = ", keys1);
            let filters = [];
            // let title = null;
            //let value = null;
            // console.log("filters 1", this.filters);
            keys1.forEach((key) => {
                // console.log("key = ", key);
                let value = this.facet_names[key];
                //console.log("value : ", value);
                if (value[1] === "show") {
                    let title = value[0];
                    //console.log("show title = ", title);
                    if (result_filters[key]) {
                        // console.log("key = ", key);
                        // console.log("show title = ", title);
                        // console.log("filter value = ", result_filters[key]);
                        filters.push({ [title]: result_filters[key] });
                    }
                }
            });
            console.log("Search filters : ", filters);
            return filters;
        },

        // Gather the post body information
        setupPostContent() {
            let post_content = { search: [] };
            post_content.limit = this.items_per_page;
            post_content.offset = this.offset;
            post_content.sort_str = this.sort_str;

            if (this.quick_search_string.length > 0) {
                post_content.search.push({
                    group: "and",
                    and: [],
                    or: [
                        {
                            field: "keywords",
                            string: this.quick_search_string,
                            type: "simple",
                        },
                        {
                            field: "abstract_data",
                            string: this.quick_search_string,
                            type: "simple",
                        },
                        {
                            field: "title",
                            string: this.quick_search_string,
                            type: "simple",
                        },
                        {
                            field: "locator_data",
                            string: this.quick_search_string,
                            type: "match",
                        },
                        {
                            field: "authors.familyName",
                            string: this.quick_search_string,
                            type: "simple",
                        },
                        {
                            field: "authors.givenName",
                            string: this.quick_search_string,
                            type: "simple",
                        },
                        {
                            field: "target_audience",
                            string: this.quick_search_string,
                            type: "simple",
                        },
                        {
                            field: "author_org.name",
                            string: this.quick_search_string,
                            type: "simple",
                        },
                    ],
                });
            } else {
                post_content.search.push({
                    group: "and",
                    and: [],
                    or: [{ field: "id", string: "*", type: "simple" }],
                });
            }

            return post_content;
        },
    },

    // Lifecycle functions, see
    // https://v3.vuejs.org/guide/instance.html#lifecycle-diagram for more info
    mounted() {
        this.getSearchResults();
    },

    // beforeUpdate() {
    //     console.log("beforeUpdate: ", this.is_querying);
    //     //this.setupFilters();
    // },
};
</script>

